import React from "react";
import { useSelector } from "react-redux";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";
import { faArrowUpRightFromSquare } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


function PortList() {
	let data=useSelector(state => state.portfolio);
	return (
		<ul className="worklst">
			{data.map((d,i) => 
				<li key={i+1} className="work_lst_item">
					<a href={d.page} target="_blank" className="lk_img">
						<img src={d.image} />
					</a>
					<div className="info">
						<div className="hash">
							<strong>{i+1}</strong>
							<em><br /></em>
						</div>
						<h2 className="company">{d.h2}</h2>
						<span className="tit">  </span>
						<div className="flex">
							<a href={d.detail} target="_blank" className="text1">
								<pre> </pre>
								<FontAwesomeIcon icon={faArrowRight} />
							</a>
							<a href={d.page} target="_blank" className="text2">
								<pre> </pre>
								<FontAwesomeIcon icon={faArrowUpRightFromSquare} />
							</a>
						</div>
					</div>
				</li>
			)}
		</ul>
	);
}

export default PortList;
