import { useSelector } from 'react-redux';


function Skill() {

	let data=useSelector(state => state.skills);
	return (
		<div className="skill">
			<h3 className="tit">SKILL</h3>
			<div className="textbox">
				<ul>
					{data.map((d,i) => 
					<li key={i+1}>
						<h2>{d.h2}</h2>
						<div className="comment">
							{d.span.map((d,i) => <span key={i+1}>{d}</span>)}
						</div>
					</li>
					)}
				</ul>
			</div>
		</div>
	);
}

export default Skill;
